export default {
	ask : function( name , id ){ //
		if( name && id ){
			jrj.jsCallNative(125, JSON.stringify({ username: name, userid: id }));
		}else{//
			jrj.jsCallNative(125, '');
		}
	}
	,login : function( n ){ //  -1  -2 accessToken("accessToken", "115","AccessToken"),
		var num = n == -1 ? 108 : 115
		jrj.jsCallNative(num, '');
	}
}


